<template>
<el-container class='container'>
  <el-header>
    <HeaderTitle/>
  </el-header>
  <div style="height: calc(100% - 41px)">
   <div class='tab-container'>
      <ul
        class="tabUl"
      >
        <li
          v-for="(tab,index) in selectTabs"
          @click="toggleN(index,tab.type)"
          :class="{activeN:activeN ==index}"
          :key="index"
        >{{tab.name}}</li>
    </ul>
    </div>
    <el-main v-loading='loading'>
    <div class='top-container'>
      <el-card>
        <p class='title'>{{title.stackUsed[type]}}</p>
        <h5>{{topData.stackUsed}}
          <span>{{unit[type]}}</span>
        </h5>
      </el-card>
      <el-card>
         <h5>{{topData.lastUsed}}
           <span>{{unit[type]}}</span>
         </h5>
         <p class='title'>
           <span >昨日环比前日</span>
           <span>
             <i v-if="topData.compared>0"
               class="el-icon-caret-top"
               :style="{color:'red'}" />
             <i v-else class="el-icon-caret-bottom"
              :style="{color:'green'}"/>
             <span>{{topData.compared}}</span>
           </span>
         </p>
      </el-card>
      <el-card v-for="(item,index) in alarmList" :key="index">
        <p class='title'>{{item.name}}</p>
        <h5 class='alarm'>
          {{item.value}}
          <span>次</span>
        </h5>
      </el-card>
      <el-card>
        <p v-for="(info,index) in alarmInfoList" :key="index">
          {{info}}
        </p>
      </el-card>
    </div>

    <div class='main-chart-container'>
      <div class='left-container' v-loading='mapLoading'>
        <el-card class='pie-table-container'>
          <div slot="header" class="clearfix">
            <span class='title'>{{pieTitle}}</span>
             <el-link type="primary"
             :underline="false"
              @click="showMore">查看更多</el-link>
            <el-select v-model="groupType" size='mini'>
              <el-option
                v-for="item in groupTabs"
                :key="item.type"
                :label="item.name"
                :value="item.type">
              </el-option>
            </el-select>

          </div>
          <div class='inner-container'>
            <div class='pie-chart' ref="pieChart"></div>
                <vxe-grid
                  border='none'
                  show-overflow
                  highlight-hover-row

                  :show-header="false"
                  :columns="tableColumns"
                  ref="xGrid"
                  :data="chartData"
                  cell-class-name='grid-cell'
                ></vxe-grid>
          </div>
        </el-card>
        <el-card class='rank-container'>
           <div slot="header" class="clearfix title">
            {{rankTitle}}
          </div>
          <div class='list-group'>
            <el-row v-for="(item,index) in rankData"
              :key="index"
              :gutter="20">
              <el-col v-if="index>2" :span="5">{{index+1}}</el-col>
              <el-col v-else :span="5">
                <img :src="require(`../../../assets/img/energy/rank/${index+1}.png`)" />
              </el-col>
              <el-col :span="12">{{item.name}}</el-col>
              <el-col :span="7">{{item.value}}</el-col>
              </el-row>
          </div>
        </el-card>
      </div>

        <el-card class='history-container right-container'
          :body-style="{width:'100%'}">
          <div slot="header" class="clearfix title">
            {{title.history[this.type]}}
          </div>
          <div ref='barChart' class='bar-chart'></div>
        </el-card>

    </div>
</el-main>
   </div>

   <el-dialog
    :title='pieTitle'
    :visible.sync="more"
    @opened='dialogOpen'
    width="850px"

    >
     <div class='inner-container'>
        <div class='pie-chart' ref="dialogPieChart"></div>
          <VXETable
            class="more-table"
            :rowStyle="rowStyle"
            :headerStyle="headerStyle"
            border="none"
            show-overflow
            :stripe="false"
            highlight-hover-row
            :tableName="''"
            :show-header="true"
            :tableColumns="allTableColumns"
            :tableDatas="tableDatas"
            :tablePage="tablePages"
             @onTablePageChange="onTablePageChanges"
            cell-class-name='grid-cell'
          ></VXETable>
          </div>
   </el-dialog>
</el-container>
</template>

<script>
import js from '@/assets/js/energy/EnergyAll/index';
export default js;
</script>
<style lang="less" scoped>
@import '../../../assets/less/energy/EnergyAll/index.less';
</style>
