import { showErrorNotify } from '@/utils/notify';
import { GAS_NAME } from '@/utils/ajaxConfigure';
import { showWarningMessage } from '@/utils/message';

import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import VXETable from '@/components/common/Table.vue';
// import axios from 'axios';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_STATUS_GANTT,
  ENERGY_TYPE
} from '@/utils/constantsFile';

import { GetEnergyComparedInfo, GetEnergyDistributionInfo, GetEnergyHistoryInfo, GetWarningRecord } from '@/api/energyAll.js';

export default {
  name: 'EnergyAll',
  components: {
    HeaderTitle,
    VXETable
  },
  data() {
    return {
      GAS_NAME: GAS_NAME,
      EQUIPMENT_STATUS_GANTT: EQUIPMENT_STATUS_GANTT,
      BTN_TEXT: BTN_TEXT,
      activeN: 0,
      // 水/电/气 类别
      type: 0,
      // 车间/产线/表 类别
      groupType: 0,
      loading: false,
      // 分布图及排名加载
      mapLoading: false,
      selectTabs: [
        {
          type: 0,
          name: '电表'
        },
        {
          type: 1,
          name: '水表'
        },
        {
          type: 2,
          name: GAS_NAME + '表'
        }
      ],
      groupTabs: [
        {
          type: 0,
          name: '表计'
        },
        {
          type: 1,
          name: '产线'
        },
        {
          type: 2,
          name: '车间'
        }
      ],
      color: [
        'RGBA(232, 89, 98, 1)',
        'RGBA(251, 155, 78, 1)',
        'RGBA(8, 113, 220, 1)',
        'RGBA(162, 200, 75, 1)',
        'RGBA(35, 217, 219, 1)',
        'RGBA(182, 162, 222, 1)'
      ],
      unit: ['KW·H', 'm³', 'm³'],
      title: {
        stackUsed: ['今年累计用电量', '今年累计用水量', '今年累计用' + GAS_NAME + '量'],
        history: ['用电历史记录', '用水历史记录', '用' + GAS_NAME + '历史记录']
      },
      topData: {
        stackUsed: 0, // 今年累计
        lastUsed: 0, // 作日用量
        compared: 0 // 作日环比前日
      },
      alarmList: [
        {
          name: '今日告警',
          value: 0
        },
        {
          name: '本月告警',
          value: 0
        }
      ],
      alarmInfoList: ['没有告警记录'],
      tableName: [
        ['表计', '产线', '车间'],
        ['用电占比', '用水占比', '用' + GAS_NAME + '占比'],
        ['用电量', '用水量', '用' + GAS_NAME + '量']
      ],

      allData: [],
      // 用电历史记录
      barData: [],
      // 查看更多表格数据
      tableDatas: [],
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevPage',
          'Number',
          'NextPage',
          'Total'
        ]
        // perfect: true
      },
      // 查看更多对话框
      more: false,
      pieChart: null,
      barChart: null
    };
  },
  computed: {
    pieTitle: function() {
      return `用${this.selectTabs[this.type].name.slice(0, 1)}情况${this.groupType <= 1 ? this.groupTabs[this.groupType].name : this.selectTabs[this.type].name}分布图`;
    },
    rankTitle: function() {
      return `本月${this.groupType <= 1 ? this.groupTabs[this.groupType].name : this.selectTabs[this.type].name}用${this.selectTabs[this.type].name.slice(0, 1)}量排名`;
    },
    // 分布图表格
    tableColumns: function() {
      const firstName = this.groupType < 1 ? 'sbName' : 'fzName';
      return [
        {
          field: firstName,
          title: this.tableName[0][this.groupType],
          align: 'center',
          width: '150px',
          slots: {
            default: ({ row, rowIndex, column }, h) => {
              return [<div class='name-cell'>
                <span style={{ 'background-color': this.color[rowIndex] || 'transparent' }} class='color-box'></span>
                <span>{row[column.property]}</span>
              </div>];
            }
          }
        }, {
          field: 'proportion',
          title: this.tableName[1][this.type],
          align: 'center',
          width: '100px',
          slots: {
            default: ({ row, rowIndex, column }, h) => {
              return numFormat(row[column.property]) + '%';
            }
          }
        }
      ];
    },
    // 查看更多分布图表格
    allTableColumns: function() {
      const firstName = this.groupType < 1 ? 'sbName' : 'fzName';
      return [
        {
          field: firstName,
          title: this.tableName[0][this.groupType],
          align: 'center',
          slots: {
            default: ({ row, rowIndex, column }, h) => {
              return [<div class='name-cell'>
                <span style={{ 'background-color': this.color[rowIndex] || 'transparent' }} class='color-box'></span>
                <span>{row[column.property]}</span>
              </div>];
            }

          }
          // width: '100px'
        }, {
          field: 'proportion',
          align: 'center',
          title: this.tableName[1][this.type],
          slots: {
            default: ({ row, rowIndex, column }, h) => {
              return numFormat(row[column.property]) + '%';
            }
          }
          // width: '100px'
        }, {
          field: 'totalNumber',
          align: 'center',
          title: `${this.tableName[2][this.type]}(${this.unit[this.type]})}`
          // width: '100px'
        }
      ];
    },
    // 排名数据
    rankData: function() {
      return this.chartData.map((el, index) => ({
        name: el.sbName || el.fzName,
        value: numFormat(el.totalNumber) + this.unit[this.type],
        url: index < 3 ? `../../../assets/img/energy/rank/${index + 1}.png` : undefined
      }));
    },
    // 分布图数据
    chartData: function() {
      return this.allData.slice(0, 6);
    }

  },
  watch: {
    barData: function() {
      this.drawBar();
    },
    allData: function() {
      this.drawPie(this.$refs.pieChart);
    },
    type: function() {
      this.init();
    },
    groupType: function() {
      this.getMapData();
    }
  },
  methods: {
    /**
     * @description: 切换设备tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggleN(i, type) {
      this.activeN = i;
      this.type = type;
    },

    showMore() {
      this.more = !this.more;
      // this.drawPie(this.$refs.dialogPieChart);
    },
    headerStyle() {
      return {
        backgroundColor: 'RGBA(238, 245, 253, 1)'
      };
    },
    rowStyle({ row, rowIndex }) {
      if (rowIndex % 2 === 1) {
        return {
          backgroundColor: 'RGBA(238, 245, 253, 1)'
        };
      }
    },
    /**
     * @description: 查看更多对话框获取数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    dialogOpen() {
      const { currentPage, pageSize } = this.tablePages;
      this.drawPie(this.$refs.dialogPieChart);
      this.tableDatas = this.allData.slice((currentPage - 1) * pageSize, currentPage * pageSize);
    },
    /**
     * @description: 获取饼图-分布图
     * @param {*} elem
     * @return {*}
     * @author: zhxy
     */
    drawPie(elem) {
      this.pieChart = this.$echarts.init(elem);
      const option = {
        tooltip: {
          trigger: 'item'
        },

        series: [
          {
            type: 'pie',
            name: '',

            label: {
              show: false,
              position: 'indside'
            },

            radius: ['30%', '70%'],
            center: ['50%', '50%'],
            roseType: 'angle',
            data: this.allData.map((el, index) => {
              return {
                name: el.sbName || el.fzName,
                value: el.totalNumber,
                itemStyle: {
                  color: this.color[index]
                }
              };
            })
          }
        ]
      };

      this.pieChart.setOption(option, true);
      window.addEventListener('resize', () => {
        this.pieChart.resize();
      });
    },
    /**
     * @description: 7天历史记录柱状图
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    drawBar() {
      this.barChart = this.$echarts.init(this.$refs.barChart);
      const option = {
        tooltip: {
          trigger: 'item'
        },
        yAxis: {
          type: 'value',
          name: this.unit[this.type]
        },
        xAxis: {
          type: 'category',
          name: '日',
          data: this.barData.days.map((el) => {
            return this.moment(el).format('MM-DD');
          })
        },
        series: [
          {
            type: 'bar',
            barMaxWidth: 30,
            color: 'rgba(8, 113, 220, 1)',
            name: this.title.history[this.type],
            label: {
              show: true,
              position: 'top',
              color: 'auto'
            },
            data: this.barData.nums.map(numFormat)
          }
        ]
      };
      this.barChart.setOption(option, true);
      window.addEventListener('resize', () => {
        this.barChart.resize();
      });
    },
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.tableDatas = this.allData.slice((currentPage - 1) * pageSize, currentPage * pageSize);
    },

    async init() {
      this.loading = true;
      await Promise.all([
        this.getMapData(),
        this.getBarData(),
        this.getWarnInfo(),
        this.getCompareInfo()
      ]);
      this.loading = false;
    },
    /**
     * @description: 获取分布图及排名数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getMapData() {
      try {
        this.mapLoading = true;
        const LEVEL_TYPE = ['DEVICE', 'LINE', 'WORKSHOP'];
        const data = await GetEnergyDistributionInfo({
          energyType: ENERGY_TYPE[this.activeN],
          checkLevelType: LEVEL_TYPE[this.groupType]
        });
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.allData = data.rows;
          this.tablePages.total = data.rows.length;
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.mapLoading = false;
      }
    },
    /**
     * @description: 获取7天历史记录数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getBarData() {
      try {
        const data = await GetEnergyHistoryInfo({
          energyType: ENERGY_TYPE[this.activeN]
        });
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.barData = data.rows;
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      }
    },
    /**
     * @description: 获取告警记录数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getWarnInfo() {
      try {
        const data = await GetWarningRecord({
          energyType: ENERGY_TYPE[this.activeN]
        });

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.alarmList = [
            { name: '今日告警', value: data.rows.todayNum },
            { name: '本月告警', value: data.rows.totalNum }
          ];
          if (data.rows.data.length > 0) {
            this.alarmInfoList = data.rows.data.slice(-3).reverse().map((el) => (
              `${this.moment(el.nygjTime).format('MM-DD hh:mm')}:${el.deviceName}本小时用量${el.nygjThisHourMeter},上限为${el.nygjUpperLimit}`
            ));
          } else {
            this.alarmInfo = ['今日没有告警记录'];
          }
        } else {
          // showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      }
    },
    /**
     * @description: 获取累计、作日环比数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getCompareInfo() {
      this.topData = {
        stackUsed: 0, // 今年累计
        lastUsed: 0, // 作日用量
        compared: 0 // 作日环比前日
      };
      try {
        const data = await GetEnergyComparedInfo({
          energyType: ENERGY_TYPE[this.activeN]
        });
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.topData = {
            stackUsed: data.rows.totalAmount,
            lastUsed: data.rows.amount,
            compared: data.rows.chain === 'NaN' ? '-' : numFormat(data.rows.chain) + '%'
          };
        } else {
          // showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      }
    }
  },
  mounted() {
    this.init();
  }
};
function numFormat(num) {
  return parseFloat(Number(num).toFixed(2));
}
